/**
 * @see https://github.com/w8r/Leaflet.Path.Transform 
 * 原插件不支持点的旋转，特此修改
 */
import VectorMarker from '@/feature/VectorMarker';
//
let { _createRotationHandlers } = L.Handler.PathTransform.prototype;
/**
 * 
 */
L.Handler.PathTransform.include({
  /**
   * 旋转句柄
   */
  _createRotationHandlers() {
    let { _map } = this;
    if (!this._rect) {

    }
    if (this._rect instanceof VectorMarker) {
      let { _latlng } = this._rect;

      let lineOffset = [0, -16]
      let topPoint = _map.unproject(_map.project(_latlng).add(lineOffset));
      //
      let offset = [0, -30];
      let handlerPosition = _map.unproject(_map.project(_latlng).add(offset));
      //
      this._handleLine = new L.Polyline([topPoint, handlerPosition],
        this.options.rotateHandleOptions).addTo(this._handlersGroup);
      //
      this._rotationMarker = new L.PathTransform.RotateHandle(handlerPosition,
        this.options.handlerOptions)
        .addTo(this._handlersGroup)
        .on('mousedown', this._onRotateStart, this);

      this._rotationOrigin = _latlng.clone();
      this._handlers.push(this._rotationMarker);
    } else {
      _createRotationHandlers.call(this)
    }

  },
  /**
   * Bounding polygon
   * @return {L.Polygon}
   */
  _getBoundingPolygon: function () {
    let { _path } = this;
    if (this._rectShape && this._rectShape.geometry.type !== 'Point') {
      return L.GeoJSON.geometryToLayer(
        this._rectShape, this.options.boundsOptions);
    } else if (_path.getBounds) {
      return new L.Rectangle(
        _path.getBounds(), this.options.boundsOptions);
    } else {//添加点的旋转
      let { _latlng } = _path;
      _latlng = _latlng.clone();
      let marker = new VectorMarker(_latlng, {
        src: 'rect',
        radius: 32,
        radiusY: 32,
        weight: 2,
        color: 'red',
        fill: false
      });
      //
      marker._latlngs = [[_latlng, _latlng, _latlng, _latlng]]
      //
      return marker;
    }
  }
})


