let {
  LayerGroup,
  FeatureGroup
} = L;
// 隐藏数据集

/**
 * 
 */
FeatureGroup.include({
  /**
   * 
   * @param {*} map 
   */
  onAdd(map) {
    this._hideLayers = [];
    this.eachLayer(map.addLayer, map);
    this.on('layeradd', this.checkInZoomOnAdd, this)
    map.on('zoomend moveend', this.checkByZoom, this)
  },
  /**
   * 
   * @param {*} map 
   */
  onRemove(map) {
    this._hideLayers = [];
    this.eachLayer(map.removeLayer, map);
    this.off('layeradd', this.checkInZoomOnAdd, this)
    map.off('zoomend moveend', this.checkByZoom, this)
  },
  /**
   * 
   */
  checkLayerInZoom(layer) {
    let map = this._map;
    let zoom = map.getZoom();
    let {
      _hideLayers
    } = this;
    if (layer) {
      let {
        options
      } = layer;
      if (options.minZoom > zoom || options.maxZoom < zoom) {
        !_hideLayers.includes(layer) && _hideLayers.push(layer);
        map.removeLayer(layer);
      } else if (_hideLayers.includes(layer)) {
        map.addLayer(layer);
      }
    }
  },
  /**
   * 
   * @param {*} e
   */
  checkInZoomOnAdd({
    layer
  }) {
    this.checkLayerInZoom(layer);
  },
  /**
   * 
   */
  checkByZoom() {
    this._hideLayers.filter(layer => this.hasLayer(layer));
    this.eachLayer(this.checkLayerInZoom, this)
  },
  // /**
  //  * 
  //  * @param {*} layer 
  //  */
  // addLayer(layer) {

  // },

  // removeLayer(layer) {

  // }

})



